<template>
  <div class="home">
    <navBar></navBar>
    <swiper :options="swiperOption" class="planListScroll" ref="mySwiper">
      <swiper-slide v-for="(item,index) in list" :key='index'>
        <img class="thumbImg" :src="item" />
      </swiper-slide>
    </swiper>
    <div class="home-main">
        <!-- 精品课程 -->
        <div class="main-item-title">
          <h2>精品课程</h2>
          <span>MORE</span>
        </div>
        <div class="excellent-courses">
          <img src="../../.././static/home/home01.jpg" />
          <img src="../../.././static/home/home02.jpg" />
        </div>

        <!-- 科技前沿 -->
        <div class="main-item-title">
          <h2>科技前沿</h2>
          <span>MORE</span>
        </div>
        <div class="technology">
            <ul>
              <li v-for="(item,index) of technologyList" :key="index">
                  <img :src="item.src" />
                  <p v-if="item.title">{{item.title}}</p>
              </li>
            </ul>
        </div>

        <!-- 精品微课 -->
        <div class="main-item-title">
          <h2>精品微课</h2>
          <span>MORE</span>
        </div>
        <div class="small-courses">
          <ul>
            <li v-for="(item,index) of courseList" :key="index">
                <img :src="item.src" />
                <div class="content">
                  <h4 v-if="item.title">{{item.title}}</h4>
                  <p>{{item.summary}}</p>
                  <div class="bottomPart">
                    <div>
                      <span class="iconfont icon-eye"></span>
                      <span>{{item.num}}</span>
                    </div>
                    <div>
                      <span class="iconfont icon-date"></span>
                      <span>{{item.date}}</span>
                    </div>
                  </div>
                </div>
            </li>
          </ul>
        </div>

        <!-- 职场修炼术 -->
        <div class="main-item-title">
          <h2>职场修炼术</h2>
          <span>MORE</span>
        </div>
        <div class="workPlace">
          <ul>
            <li v-for="(item,index) of workList" :key="index">
                <img :src="item.src" />
                <p v-if="item.title">{{item.title}}</p>
                <div v-if="item.title">
                  <span class="iconfont icon-eye"></span>
                  <span>{{item.num}}</span>
                </div>
            </li>
          </ul>
        </div>

        <!-- 排行榜 -->
        <div class="rank">
            <ul class="rank-ul">
              <li class="rank-li" v-for="(item,index) of 3" :key="index">
                <div class="rank-header">
                  <img src="../../../static/home/rank01.png" />
                  <div>
                    <h3>查看全部&gt;&gt;</h3>
                    <h2>课程排行榜</h2>
                  </div>
                </div>
                <div class="rank-content">
                  <ul class="rank-content-ul">
                    <li class="rank-content-li" v-for="(item,index) of rankList" :key="index">
                        <div class="li-left">
                          <img class="icon" v-if="index === 0" src="https://zxy9.zhixueyun.com/images/rank-style3-first.png"/>
                          <img class="icon" v-if="index === 1" src="https://zxy9.zhixueyun.com/images/rank-style3-second.png"/>
                          <img class="icon" v-if="index === 2" src="https://zxy9.zhixueyun.com/images/rank-style3-third.png"/>
                          <span class="icon" v-if="index > 2">{{index + 1}}</span>
                          <img class="avatar" src="https://zxy9.zhixueyun.com/images/default-cover/default_user.png"/>
                          <div class="name-label">
                            <div class="name">{{item.name}}</div>
                            <div class="label">{{item.label}}</div>
                          </div>
                        </div>
                        <span class="li-right">223门</span>
                    </li>
                  </ul>
                </div>
              </li>
            </ul>
        </div>
    </div>

    <!-- 讲师榜 -->
    <div class="teacher-special">
      <div class="home-main">
        <div class="main-item-title">
          <h2>讲师榜</h2>
          <span>MORE</span>
        </div>
        <div class="teacher">
          <ul>
            <li v-for="(item,index) of teacherList" :key="index">
                <img :src="item.src" />
                <h3>{{item.title}}</h3>
                <div class="level">高级</div>
                <div>知学云</div>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <bottomPart></bottomPart>
  </div>
</template>

<script>
import { swiperList,technologyList,workList,courseList,teacherList,rankList } from './data.js'
export default {
    name: "home",
    data() {
        return {
          swiperOption: {
              autoplay: true,
              slidesPerView: "auto",
              loop: true, //循环
              grabCursor: true //小手掌抓取滑动
          },
          list:swiperList(),
          technologyList:technologyList(),
          workList:workList(),
          courseList:courseList(),
          teacherList:teacherList(),
          rankList:rankList()
        };
    },
    components: {

    }
};
</script>

<style lang="scss" scoped>
.planListScroll{
  height: 342px;
  .thumbImg{
    width:100%;
    height: 342px;
  }
}
.teacher-special{
    background: #f5f5f5;
    padding-bottom: 50px;
    margin-bottom: 60px;
  }
.home-main{
  width: 1200px;
  margin: 0 auto;
  //统一标题
  .main-item-title{
    display:flex;
    justify-content: space-between;
    align-items:center;
    padding: 50px 0 20px 0;
    h2{
      font-size:20px;
    }
    span{

      &:hover{
        color:blue;
        cursor:pointer;
      }
    }
  }
  //精品课程
  .excellent-courses{
     display: flex;
     justify-content: space-between;
     img{
       width: 49%;
       height: 88px;
       border-radius: 6px;
     }
  }
  //科技前沿
  .technology{
    ul{
      display: grid;
      grid-template-columns: repeat(5, 1fr);
      grid-gap:20px;
      grid-auto-rows:140px;
      li{
        img{
          width: 100%;
          height: 120px;
          border-radius: 6px;
        }
        p{
          padding: 10px 0;
          color: #999;
        }
        &:first-of-type{
          grid-row:span 2;
          grid-column:span 1;
          background: #999;
          img{
            width: 100%;
            height: 100%;
            border-radius: 6px;
          }
        }
      }
    }
  }
  // 精品微课
  .small-courses{
      ul{
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-gap:20px;
        grid-auto-rows:170px;
        li{
          background: #f5f5f5;
          border-radius: 8px;
          overflow: hidden;
          .content{
            padding: 20px;
              h4{
                margin-bottom: 10px;
              }
              .bottomPart{
                display: flex;
                align-items: center;
                div{
                  font-size: 8px;
                  color: #999;
                }
                div:first-of-type{
                  margin-right: 20px;
                }
              }
          }
          p{
            margin-bottom: 10px;
            color: #999;
            line-height: 1.5;
          }
          &:nth-child(1){
              grid-row:span 2;
              grid-column:span 2;
              img{
                width: 100%;
                height: 264px;
              }
              .content{
                p{
                  @include oneText;
                }
              }
          }
          &:nth-child(2){
              grid-row:span 1;
              grid-column:span 2;
              display: flex;
              img{
                width: 190px;
                height: 170px;
              }
              .content{
                p{
                  @include muchText(2);
                }
              }
          }
          &:nth-child(3){
              grid-row:span 2;
              grid-column:span 1;
              img{
                width: 100%;
                height: 170px;
              }
              .content{
                p{
                  @include muchText(4);
                }
              }
          }
          &:nth-child(4){
              grid-row:span 1;
              grid-column:span 2;
              display: flex;
              img{
                width: 190px;
                height: 170px;
                order:2;
              }
              .content{
                order:1;
                p{
                  @include muchText(3);
                }
              }
          }
        }
      }
  }
  //职场修炼术
  .workPlace{
    ul{
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-gap:20px;
      grid-auto-rows:200px;
      li{
        img{
          width: 100%;
          height: 132px;
          border-radius: 6px;
        }
        p{
          padding: 10px 0;
          color: #999;
        }
        div{
          color: #999;
          .iconfont{
            font-size: 6px;
          }
        }
        &:first-of-type{
          grid-row:span 1;
          grid-column:span 2;
          background: #999;
          img{
            width: 100%;
            height: 100%;
            border-radius: 6px;
          }
        }
      }
    }
  }
  //排行榜
  .rank{
    margin: 20px 0;
    .rank-ul{
      display:flex;
      justify-content:space-between;
      .rank-li{
        cursor: pointer;
        .rank-header{
          position:relative;
          width:310px;
          height:100px;
          img{
            width:100%;
            height:100%;
          }
          div{
            position:absolute;
            top:50%;
            transform: translateY(-50%);
            left:42%;
            h2{
              font-size: 20px;
              font-weight: 500;
              color: #fff;
              white-space: nowrap;
              overflow: hidden;
              line-height: 30px;
            }
            h3{
              font-size: 16px;
              font-weight: 400;
              color: rgba(255,255,255,.5);
              line-height: 30px;
            }
          }
        }
        .rank-content{
          .rank-content-ul{
            .rank-content-li{
              display:flex;
              justify-content:space-between;
              align-items: center;
              height: 80px;
              border-bottom: 1px #eee solid;
              .li-left{
                display: flex;
                align-items: center;
                  .icon{
                    width:35px;
                    height:35px;
                    border-radius:50%;
                    text-align: center;
                    line-height: 35px;
                    background: #ccc;
                    color: #fff;
                  }
                  .avatar{
                    width:40px;
                    height:40px;
                    border-radius:50%;
                    margin: 0 20px;
                  }
                  .name-label{
                    .name{
                      margin-bottom: 20px;
                    }
                    .label{
                      color:#999;
                    }
                  }
              }
              .li-right{
                padding:2px 4px;
                color:#999;
                background:#f5f5f5;
                border-radius:4px;
                font-size: 8px;
              }
            }
          }
        }
      }
    }
  }
  // 讲师榜
  .teacher{
    margin-top: 60px;
     ul{
       display: flex;
       justify-content: space-between;
       li{
          background: #fff;
          width: 200px;
          height: 225px;
          text-align: center;
          img{
            width: 100px;
            height: 100px;
            border-radius: 50%;
            margin: -30px auto 30px auto;
          }
          .level{
            color: orange;
            margin: 10px 0;
          }
       }
     }
  }
}
</style>



